.main-title {
  text-align: center;
  h2 {
    position: relative;
    text-transform: uppercase;
    font-size: 4rem;
    font-weight: 700;
    color: var(--primary-100);
    span {
      color: var(--secondary-color);
    }
    .bg-text {
      position: absolute;
      top: 50%;
      left: 50%;
      color: var(--primary-600-opacity);
      transition: var(--transition-1);
      z-index: -1;
      transform: translate(-50%, -50%);
      font-weight: 800;
      font-size: 6.3rem;
      white-space: nowrap;
    }
  }
}

// Typewriter effect
.typewriter {
  overflow: hidden; // Ensures the content is not revealed until the animation
  border-right: 0.15em solid var(--secondary-color); // The typwriter cursor
  white-space: nowrap; // Keeps the content on a single line
  margin: 0 auto; // Gives that scrolling effect as the typing happens
  letter-spacing: 0.15em; // Adjust as needed
  // animation: typing 4s steps(80, end) 2s 1 normal both,
  animation: 4s steps(50) 2s both typing, blink-caret 1s step-end infinite;
  max-width: fit-content;
}

// The typing effect
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

// The typewriter cursor effect
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: var(--secondary-color);
  }
}
